
import {
    getCurrentLanguage,
    MultiLanguageChange,
} from '../Infra/Language'

import {
    Endpoints,
    socialID_Input,
} from '../Infra/Endpoints'

import {
    SetLanguageHTMLSelect,
    updateHTML,
    IsLoading,
    loadingOn,
    loadingOff,
    postPublicPortal,
    setToSession,
} from '../Infra/Util'

import PasswordField from '../Components/Fields/Password'

import Form1 from '../Components/Forms/Register/Register_1'
import Form2 from '../Components/Forms/Register/Register_2'

function GetDefinitions() {
    return { // reserved words
        currentView: 'currentView',
        form1: 'form1',
        form2: 'form2',
    }
}

function GetPageData() {
    return {
        socialID: '',
        nameID: '',
        emailID: '',
    }
}

export default class {

    getHtml() { return "<div id='currentForm'></div>" }

    constructor(params) {

        // -----------------------------------------------------------------------------
        // page events here --------------------------------------------------------
        // -----------------------------------------------------------------------------

        $(document).ready(function () {
            var defs = GetDefinitions();
            UpdateNewView(defs.form1);
            SetLanguageHTMLSelect()
            PushPageData(GetPageData());
        });

        $(document).on('change', '#languageSel', function () {
            if (MultiLanguageChange($('#languageSel').val()))
                setTimeout(() => { location.href = '/register'; }, 100);
        });

        $(document).on('keydown', function (e) {
            var defs = GetDefinitions();
            var _valParams = { focus: false, msg: false, fields: null }
            switch (e.keyCode) {
                case 9: switch (GetCurrentView()) { // tab
                    case defs.form1: Form1.validate(_valParams); break;
                    case defs.form2: Form2.validate(_valParams); break;
                }
                    break;
                case 13: switch (GetCurrentView()) { // enter
                    case defs.form1: next_form1_Click(); break;
                }
                    break;
            }
        });

        document.body.addEventListener("click", e => {
            var click_id = $(e.target).attr('id');
            var defs = GetDefinitions();
            switch (GetCurrentView()) {
                case defs.form1:
                    var elements = Form1.elements();
                    switch (click_id) {
                        case elements.btnNext: next_form1_Click(); break;
                    }
                    break;
                case defs.form2:
                    var elements = Form2.elements();
                    switch (click_id) {
                        case elements.btnPrev: previous_form1_click(); break;
                        case 'seePass' + elements.passwordID: PasswordField.btnSeePassword(elements.passwordID); break;
                        case 'seePass' + elements.passwordConfID: PasswordField.btnSeePassword(elements.passwordConfID); break;
                    }
                    break;
            }
        });

        // -----------------------------------------------------------------------------
        // MAIN functions here --------------------------------------------------------
        // -----------------------------------------------------------------------------

        function GetCurrentView() { return sessionStorage.getItem(GetDefinitions().currentView); }
        function PushState(newState) { sessionStorage.setItem(GetDefinitions().currentView, newState); }
        function GetCurrentPageData() { return JSON.parse(localStorage.getItem('currentPageData')); }
        function PushPageData(newPageData) { localStorage.setItem('currentPageData', JSON.stringify(newPageData)); }

        function UpdateNewView(newState) {
            PushState(newState)
            var defs = GetDefinitions();            
            switch (newState) { // HTML
                case defs.form1: updateHTML('currentForm', Form1.getHtml()); break;
                case defs.form2: updateHTML('currentForm', Form2.getHtml()); break;
            }            
            switch (newState) { // POST OP
                case defs.form1: Form1.maskUp(); break;
            }
        }

        // -----------------------------------------------------------------------------
        // form1 functions
        // -----------------------------------------------------------------------------

        function next_form1_Click() {
            if (IsLoading()) return;
            var elements = Form1.elements();
            var formData = socialID_Input(
                $('#' + elements.socialID).val(),
                getCurrentLanguage()
            );
            loadingOn('#' + elements.btnNext)

            // mock!! ------------
            setToSession('mockServer', JSON.stringify({ payload: true, obj: {} }));
            // mock!! ------------

            postPublicPortal(Endpoints().socialID, formData)
                .then(resp => {
                    if (resp.ok == true)
                        next_form1_flow()
                    else
                        displaySystemPopup(MultiLanguage(5), resp.msg);
                })
                .catch(resp => {
                    displaySystemPopup(MultiLanguage(5), resp.msg)
                });
        }

        function next_form1_flow() {
            loadingOff()
            var pageData = GetCurrentPageData();
            var elements = Form1.elements();
            pageData.socialID = document.getElementById(elements.socialID).value
            pageData.nameID = document.getElementById(elements.nameID).value
            PushPageData(pageData);
            UpdateNewView(GetDefinitions().form2);
        }

        // -----------------------------------------------------------------------------
        // form2 functions
        // -----------------------------------------------------------------------------

        function previous_form1_click() {
            UpdateNewView(GetDefinitions().form1);
            var pageData = GetCurrentPageData();
            var elements = Form1.elements();
            document.getElementById(elements.socialID).value = pageData.socialID
            document.getElementById(elements.nameID).value = pageData.nameID
        }
    }
}
