
import { MultiLanguage } from '../../Infra/Language'

import {
    isFieldContentValid,
    errorField,
    displaySystemPopup,
    errorClean
} from '../../Infra/Util'

export default class {

    static getHtml(id, placeholderMsg) {
        return `<div class="form-row no-padding">
                <i class="fa fa-bolt" id='fail${id}'></i>
                <input id="${id}" type="text" class="form-element" placeholder="${placeholderMsg}">
            </div>`;
    }

    static validate(id, _params) {

        if (!isFieldContentValid($('#' + id).val(), 16, 'string', 4)) {
            if (_params.focus == true)
                $('#' + id).focus()
            else {
                errorField('#fail' + id)
                if (_params.msg == true)
                    displaySystemPopup(MultiLanguage(5), MultiLanguage(6))
            }
            return false;
        }
        else
            errorClean('#fail' + id)
        return true;
    }

    static validateCPF(id, _params) {

        var strCPF = $('#' + id).val().replace(/\D/g, '');

        if (!this.CheckCPF(strCPF)) {
            if (_params.focus == true)
                $('#' + id).focus()
            else {
                errorField('#fail' + id)
                if (_params.msg == true)
                    displaySystemPopup(MultiLanguage(5), MultiLanguage(6))
            }
            return false;
        }
        else
            errorClean('#fail' + id)
        return true;
    }

    static CheckCPF (strCPF) {
        var Soma; var Resto;
        Soma = 0;
        if (strCPF == "00000000000") return false;
        for (let i = 1; i <= 9; i++) Soma = Soma + parseInt(strCPF.substring(i - 1, i)) * (11 - i);
        Resto = (Soma * 10) % 11;
        if ((Resto == 10) || (Resto == 11)) Resto = 0;
        if (Resto != parseInt(strCPF.substring(9, 10))) return false;
        Soma = 0;
        for (let i = 1; i <= 10; i++) Soma = Soma + parseInt(strCPF.substring(i - 1, i)) * (12 - i);
        Resto = (Soma * 10) % 11;
        if ((Resto == 10) || (Resto == 11)) Resto = 0;
        if (Resto != parseInt(strCPF.substring(10, 11))) return false;
        return true;
    }
}
